
    export default {
        data() {
            return {
                activeItem: 'architecture',
                
                swiperOptions: {
                    speed: 1000,
                    slidesPerView: 4,
                    loop: true,
                    spaceBetween: 1,

                    // Navigation arrows
                    navigation: {
                        nextEl: '.tab-carousel-next',
                        prevEl: '.tab-carousel-prev',
                    },

                    breakpoints: {
                        320: {
                            slidesPerView: 1,
                        },
                        768: {
                            slidesPerView: 2,
                        },
                        992: {
                            slidesPerView: 3,
                        },
                        1200: {
                            slidesPerView: 4,
                        }
                    }
                }
            }
        },
        methods: {
            isActive (menuItem) {
            return this.activeItem === menuItem
            },
            setActive (menuItem) {
            this.activeItem = menuItem
            }
        }
    };
